<template>
  <div id="app">
    <nav class="site-nav">
      <router-link
        to="/"
        class="button">Home</router-link>
      <!-- <a
          class="button"
          href="#sidebar">&#8801;</a> -->
      <ul>
        <li class="dropdown">
          <button type="button">Projects</button>
          <ul>
            <li
              v-for="route in projectRoutes"
              :key="route.path">
              <router-link
                :to="route.path"
                @click.native="unfocus">{{ route.title || route.name }}</router-link></li>
          </ul>
        </li>
        <li class="dropdown">
          <button type="button">Notepad</button>
          <ul>
            <li
              v-for="route in notepadRoutes"
              :key="route.path">
              <router-link
                :to="route.path"
                @click.native="unfocus">{{ route.title || route.name }}</router-link></li>
          </ul>
        </li>
      </ul>
    </nav>
    <main>
      <router-view />
    </main>
  </div>
</template>

<script>
import routes from './routes'

export default {
  name: 'App',
  data () {
    return {
      sidebarOpen: false
    }
  },
  computed: {
    projectRoutes () {
      return routes.filter(x => x.path.startsWith('/projects/'))
    },
    notepadRoutes () {
      return routes.filter(x => x.path.startsWith('/notepad/'))
    }
  },
  methods: {
    unfocus (event) {
      event.target.blur()
    },
    closeSidebar () {
      this.sidebarOpen = false
    }
  }
}
</script>

<style lang="scss">
.dropdown {
  ul {
    list-style: none;
    padding: 0;
  }

  a {
    padding: 0.5em;
    display: block;
    color: #111;
    &:hover {
      background-color: rgba(17, 17, 17, 0.1);
    }
  }
}
</style>



