const Home = () => import('@/components/Home')
const Projects = () => import('@/components/Projects')
const Notepad = () => import('@/components/Notepad')
const Chess = () => import('@/components/Chess')
const NQueens = () => import('@/components/NQueens')
const PiZero = () => import('@/components/PiZero')
const JsonMe = () => import('@/components/JsonMe')
const TicTacToe = () => import('@/components/TicTacToe')
const LineTown = () => import('@/components/LineTown')
const Gravity = () => import('@/components/Gravity')
const QuantumParticles = () => import('@/components/QuantumParticles')
const Sierpinski = () => import('@/components/Sierpinski')
const Mandelbrot = () => import('@/components/Mandelbrot')
const Fft = () => import('@/components/Fft')
const Histogram = () => import('@/components/Histogram')
const OldskoolFire = () => import('@/components/OldskoolFire')
const CssFrameworks = () => import('@/components/CssFrameworks')
const OtherProjects = () => import('@/components/OtherProjects')
const HeadlessWordPress = () => import('@/components/HeadlessWordPress')

export default [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/projects',
    name: 'Projects',
    component: Projects
  },
  {
    path: '/notepad',
    name: 'Notepad',
    component: Notepad
  },
  {
    path: '/notepad/pi-zero',
    name: 'pi-zero',
    title: 'Pi Zero W',
    component: PiZero
  },
  {
    path: '/notepad/css-frameworks',
    name: 'CssFrameworks',
    title: 'CSS Frameworks',
    component: CssFrameworks
  },
  {
    path: '/notepad/quantum-particles',
    name: 'QuantumParticles',
    title: 'Quantum Particles',
    component: QuantumParticles
  },
  {
    path: '/projects/chess',
    name: 'Chess',
    component: Chess
  },
  {
    path: '/projects/n-queens',
    name: 'n-Queens',
    component: NQueens
  },
  {
    path: '/projects/json-me',
    name: 'json-me',
    component: JsonMe
  },
  {
    path: '/projects/tic-tac-toe',
    name: 'TicTacToe',
    title: 'tic-tac-toe',
    component: TicTacToe
  },
  {
    path: '/projects/sierpinski',
    name: 'Sierpinski',
    component: Sierpinski
  },
  {
    path: '/projects/mandelbrot',
    name: 'Mandelbrot',
    component: Mandelbrot
  },
  {
    path: '/projects/fft',
    name: 'Fft',
    title: 'FFT',
    component: Fft
  },
  {
    path: '/projects/histogram',
    name: 'Histogram',
    component: Histogram
  },
  {
    path: '/projects/oldskool-fire',
    name: 'OldskoolFire',
    title: 'Oldskool Fire',
    component: OldskoolFire
  },
  {
    path: '/projects/line-town',
    name: 'LineTown',
    title: 'Line Town',
    component: LineTown
  },
  {
    path: '/projects/gravity',
    name: 'Gravity',
    title: 'Gravity',
    component: Gravity
  },
  {
    path: '/notepad/other-projects',
    name: 'OtherProjects',
    title: 'Other Projects',
    component: OtherProjects
  },
  {
    path: '/projects/headless-wordpress',
    name: 'HeadlessWordPress',
    title: 'Headless WordPress',
    component: HeadlessWordPress
  }
]



